.Home_div{

     height: 92vh;
     position: relative;
}

.home_content{
      padding: 10px;
      position: absolute;
      top:80px;
      left:20%;
      display: flex;
      justify-content: space-between;


}

.head{

     font-size: 50px;
     font-weight: 700;
     font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
     width:85%;

     background-image: linear-gradient(
        -225deg,
        #231557 0%,
        #44107a 29%,
        #ff1361 67%,
        #fff800 100%
      );

      background-size: auto auto;
      background-clip: border-box;
      background-size: 200% auto;
      color: #fff;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      animation: textclip 2s linear infinite;
      display: inline-block;

}

@keyframes textclip {
    to {
      background-position: 200% center;
    }
  }




.Quates{
    width:50%;
    font-size: small;
    color: grey;
     
}

.educator{

     font-size:22px;
     font-weight: 800;
     position: absolute;
     bottom:100px;
     left:30%;
     color: #910767;
     -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0,0,0,.2));

}



  @keyframes waviy {
    0%,40%,100% {
      transform: translateY(0)
    }
    20% {
      transform: translateY(-20px)
    }
  }




.img{

    width:60%;
}

@media (max-width:820px) {

    .home_content{
        display: block;
    }

    .educator{
        position: absolute;
        bottom:50px;
        left:30%;
   }

   .img{

     width:70%;
   } 
   
   .head{

     width:92%;
   }

   
   
}


@media (max-width:440px) {

    .educator{
        position: absolute;
        bottom:40px;
        left:20%;
   }

   .head{
        width:70%;
        font-size:30px;

    }

}